<template>
  <div class="videoer">
    <video-player
      class="video-player vjs-custom-skin"
      ref="videoPlayer"
      :playsinline="true"
      :options="playerOptions"
    ></video-player>
  </div>
</template>

<script>
export default {
  props: {
    post: Object
  },
  data() {
    return {
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "video/mp4", //git
            src: this.post.content_video[0].progressive[0].https_url //url
          }
        ],
        poster: this.post.image, //
        // width: document.documentElement.clientWidth, //
        notSupportedMessage: "", //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //
        }
      }
    };
  },
  created() {
    // this.playerOptions.sources.src = this.post.progressive[0].https_url;
    // this.playerOptions.poster = this.post.image;
  },
  computed: {
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  watch: {
    videoUrl(val) {
      if (val !== "") {
        this.$refs.videoPlayer.player.src(val);
      }
    },
    poster(n, o) {
      o;
      if (n) {
        this.$refs.videoPlayer.player.poster(n);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.video-js .vjs-icon-placeholder {
  width: 100%;
  height: 100%;
  display: block;
}
/deep/.video-js .vjs-big-play-button{
    width: 2em !important;
    height: 2em !important;
    margin-left: -1em !important;
    border-radius: 50%;
    border-width: 0px !important;
    // background-color:transparent !important;
}
/deep/.video-js .vjs-poster{
    background-size: 100%;
}

</style>